body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.App-outer {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  text-align: center;
}

.App-inner {
  flex: 1; /* This allows the hero section to take up the remaining space */
}

.App-nav {
  background-color: #282c34;
  color: white;
  padding: 20px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.App-nav h1 {
  font-size: 1.8em;
}

.App-nav ul {
  list-style: none;
  padding: 0;
  display: flex;
}

.App-nav li {
  margin: 0 15px;
}

.App-nav a {
  color: #61dafb;
  text-decoration: none;
  font-size: 1em;
}

.App-hero {
  padding: 60px 20px;
}

.App-hero h2 {
  font-size: 2.5em;
  margin-bottom: 20px;
}

.App-hero p {
  font-size: 1.2em;
  margin-bottom: 40px;
}

.App-button {
  background-color: #61dafb;
  color: white;
  padding: 10px 20px;
  text-decoration: none;
  font-size: 1em;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
}

.App-button:hover {
  background-color: #53c5e1;
  transform: translateY(-2px);
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15);
}

.App-button:active {
  background-color: #47aec7;
  transform: translateY(1px);
  box-shadow: 0 3px 4px rgba(0, 0, 0, 0.1);
}


.App-section {
  padding: 40px 20px;
  text-align: left;
}

.App-section h2 {
  font-size: 2em;
  margin-bottom: 20px;
}

.App-section p {
  font-size: 1.2em;
  line-height: 1.6;
}

.App-link {
  color: #61dafb;
  text-decoration: none;
  font-size: 1.2em;
}

.App-footer {
  background-color: #282c34;
  color: white;
  padding: 20px;
  text-align: center;
  flex-shrink: 0; /* Ensures the footer does not shrink */
}

.App-footer h2 {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.App-footer a {
  color: #61dafb;
  text-decoration: none;
}

.gender-equality-container {
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
}

.plots-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.plot-item {
  flex: 1 1 calc(50% - 20px);
  box-sizing: border-box;
  padding: 10px;
}


.dataset-selector {
    border: 1px solid #ccc; /* light gray border */
    border-radius: 5px; /* rounded corners */
    padding: 10px; /* optional: add padding inside the border */
}
